:root {
  --bg-color: #fff;
  --bg-accent-color: #f1f5f9;
  --fg-color: #000;
  --primary-color: #1d4fd7;
  --primary-accent-color: #3c83f6;
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg-color: #000;
    --bg-accent-color: #0f1729;
    --fg-color: #fff;
    --primary-color: #61a6fa;
    --primary-accent-color: #3c83f6;
  }
}

* {
  box-sizing: border-box;
}

html {
  margin: unset;
  background-color: var(--bg-color);
  color: var(--fg-color);
}

body {
  margin: unset;
  min-height: 100vh;
  flex-direction: column;
  font-family: monospace;
  font-size: 20px;
  display: flex;
}

a:link, a:visited {
  color: var(--primary-color);
}

a:hover, a:active {
  color: var(--primary-accent-color);
}

main {
  padding: 1rem;
}

#search {
  appearance: none;
  background-color: inherit;
  border: unset;
  color: inherit;
  width: 100%;
  border-bottom: 2px solid;
  padding: 1rem;
  font-family: inherit;
  font-size: 1rem;
  display: block;
}

#search:focus {
  background-color: var(--bg-accent-color);
  outline: unset;
}

.hidden {
  display: none;
}

footer {
  text-align: right;
  margin-top: auto;
  padding: 1rem;
  font-size: .85rem;
}

